import React, { Component } from 'react';
import style from './add-shopping-item.css';

class AddShoppingItem extends Component {
  constructor(props) {
    super(props);

    this.state = { value: 'Add More Items' };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }

  handleChange(event) {
    this.setState({ value: event.target.value });
  }

  handleClick() {
    this.setState({ value: '' });
  }

  handleSubmit(event) {
    alert('An item was submitted: ' + this.state.value);
    event.preventDefault();
  }

  render() {
    return (
      <>
        <div className={style.container}>
          <form onSubmit={this.handleSubmit}>
            {/* <img width="20px" className={style.mic} src={micIcon} alt="mic"/>{' '} */}
            <input
              type="text"
              className={style.textbox}
              value={this.state.value}
              onChange={this.handleChange}
              onClick={this.handleClick}
            />
          </form>
          <div className={style.resultsContainer}>
            <div className={style.resultsItem}>Yogurt Berry</div>
            <div className={style.resultsItem}>Yogurt Plain</div>
            <div className={style.resultsItem}>Yogurt Banana</div>
          </div>
        </div>
      </>
    );
  }
}

AddShoppingItem.propTypes = {};

export default AddShoppingItem;
