import React from 'react';
import PropTypes from 'prop-types';
import style from './heading.css';

class Heading extends React.Component {
  constructor(props) {
    super(props);

    this.state = { show: props.show };

    this.textFamily = this.textFamily.bind(this);
    this.hideNotification = this.hideNotification.bind(this);
  }

  textFamily() {
    fetch('/api/shoppingList/notifyFamily', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        message:
          "I'm at a grocery store! Add to the " +
          'family shopping list if you would like anything!',
      }),
    })
      .then(response => response.json())
      .then(data => {
        console.log(data);
        this.setState({
          show: false,
        });
      });
  }

  hideNotification() {
    this.setState({
      show: false,
    });
  }

  render() {
    if (this.state.show) {
      return (
        <>
          <div className={style.heading}>
            <div className={style.message}>
              You have arrived at a store. Would you like to notify your family?
            </div>
            <div className={style.buttonContainer}>
              <button
                className={style.button}
                onClick={this.textFamily}
                type="button"
              >
                Yes
              </button>
            </div>
            <div className={style.buttonContainer}>
              <button
                className={style.button}
                onClick={this.hideNotification}
                type="button"
              >
                Ignore
              </button>
            </div>
          </div>
          )
        </>
      );
    }
    return '';
  }
}

Heading.propTypes = {
  show: PropTypes.bool,
};

export default Heading;
